.filmCard {
  position: relative;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  max-width: 200px;
  height: 100%;
  overflow: hidden;
  @media (max-width: 769px) {
    max-width: 100px;
  }
  &__img {
    margin-bottom: 10px;
    overflow: hidden;
    border-radius: 10px;
    img {
      border-radius: 10px;
      width: 100%;
      height: 100%;
      object-fit: contain;
      transition: all 0.4s;
      -webkit-transition: all 0.4s;
      -moz-transition: all 0.4s;
      -ms-transition: all 0.4s;
      -o-transition: all 0.4s;
      -webkit-border-radius: 10px;
      -moz-border-radius: 10px;
      -ms-border-radius: 10px;
      -o-border-radius: 10px;
      &:hover {
        transform: scale(1.1);
        -webkit-transform: scale(1.1);
        -moz-transform: scale(1.1);
        -ms-transform: scale(1.1);
        -o-transform: scale(1.1);
      }
    }
  }
  &__rating {
    position: absolute;
    top: 5%;
    left: 5%;
    padding: 5px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    background-color: rgb(227, 77, 22);
    color: #fff;
    z-index: 10;
  }
  &__name {
    font-weight: 500;
    color: #fff;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-bottom: 5px;
    @media (max-width: 769px) {
      font-size: 12px;
    }
  }
  &__length {
    color: #fff;
    font-size: 12px;
  }
  &__info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }
}
